.media {
    display: flex;
    flex-direction: column;
    width: 200px;
    padding: 5px;
    margin: 5px 0;
    background-color: rgb(15, 15, 15);
    border-radius: 10px;
    position: relative;
    font-family: "Montserrat", sans-serif;
    margin-top: 20px;
  }
  
  .media:hover {
    background-color: #E50914;
    color: white;
    font-weight: bold;
    cursor: pointer;
  }
  .media:hover .subTitle{
      color: white;
  }
  
  @media (max-width: 550px) {
    .media {
      width: 46%;
    }
  }
  
  .poster {
    border-radius: 10px;
  }
  
  .title {
    width: 100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
  }
  
  .subTitle {
    display: flex;
    justify-content: space-between;
    padding-bottom: 3px;
    padding: 5px 2px;
    /* padding-bottom: 3px; */
    font-size: 14px;
    
  }